import { Component, Inject,ViewChild, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { MatTableDataSource, MatPaginator, MatSort,MatButton} from '@angular/material';
import { CommonService } from '../../Service/common.service';


@Component({
  selector: 'app-auctionparticipant',
  templateUrl: './auctionparticipant.component.html',
  styleUrls: ['./auctionparticipant.component.css']
})
export class AuctionparticipantComponent implements OnInit {
  lists: any = {};

  participantDetail = new MatTableDataSource();  
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  constructor(public dialogRef: MatDialogRef<AuctionparticipantComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,) { }

  ngOnInit() {

    this.participantDetail = new MatTableDataSource(this.participantDetailarray);
    this.lists.SelectedIndex = 0;
    this.lists.displayedColumns = ['name','mobile_no','email','auction_apply_date','apply_plot_number','payment_status', 'payment_dates', 'transaction_id', 'current_auction_status','bid_Status','action'];
    this.participantDetail.paginator = this.paginator;
    this.participantDetail.sort = this.sort;
  }

  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
    this.participantDetail.filter = filterValue;
  }

  participantDetailarray = [ {Name: "Raj Parmar", Mobile_No: "7742028801",Email: "test@gmail.com", Auction_Apply_Date: "04/15/2021",
  Apply_Plot_Number: "04", Payment_Status: "Due", Payment_Dates: "04/05/2021", Transaction_ID: "43555454545454", 
  Current_Auction_Status: "In Progress", BID_Status: "In Progress"},
  {Name: "Raj Parmar test", Mobile_No: "7742024477",Email: "test12345@gmail.com", Auction_Apply_Date: "04/15/2021",
  Apply_Plot_Number: "04", Payment_Status: "Due", Payment_Dates: "04/05/2021", Transaction_ID: "43555454545454", 
  Current_Auction_Status: "In Progress", BID_Status: "In Progress"}]  

}
