import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { CommonService } from '../../Service/common.service';
import { CustomValidators } from 'ng2-validation';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

@Component({
  selector: 'app-add-plot',
  templateUrl: './add-plot.component.html',
  styleUrls: ['./add-plot.component.css']
})
export class AddPlotComponent implements OnInit {

  FirstFormGroup: FormGroup;
  plottypelist = [{ name: "Agricultural" }, { name: "Residential"},{ name: "Commercial"}];
  statuslist = [{ name: "Open" }, { name: "Closed"},{ name: "OnHold"},{ name: "UnderProcess"}];
  checkUpdate = false
  ckplotno = '';
  ckplotsize = '';
  ckplottype = '';

  constructor(public comman: CommonService, public fb: FormBuilder,public dialogRef: MatDialogRef<AddPlotComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any, public common: CommonService) { }

  ngOnInit() {
    this.FirstFormGroup = this.fb.group({
      plot_no: ['', Validators.required],
      plot_size: ['', Validators.required],
      plot_type: ['', Validators.required],
      status: ['', Validators.required],
      // firstname: ['', [Validators.required, Validators.minLength(3), Validators.maxLength(20), Validators.pattern('^[a-zA-Z ]+$')]],
      // lastname: ['', [Validators.required, Validators.minLength(3), Validators.maxLength(20), Validators.pattern('^[a-zA-Z ]+$')]],
      // mobnumber: ['', [Validators.required, Validators.maxLength(10), Validators.minLength(10), Validators.pattern(/^-?(0|[1-9]\d*)?$/)]],
      // email: ['', [Validators.required, Validators.pattern('[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,3}$')]],
     
    });

    this.editplotdetail();
  }
  Addplotdetail(){
    let data = this.FirstFormGroup.value
    if(this.FirstFormGroup.valid){
      this.dialogRef.close({status:true,data});
    }else{
      this.comman.ToastMessage("Fail", "Please Fill All Detail");
    }
  }
  Updateplotdetail(){
    let data = this.FirstFormGroup.value
    if(this.FirstFormGroup.valid){
      this.dialogRef.close({status:false,data,plotno: this.ckplotno,plotsize:this.ckplotsize,plottype: this.ckplottype})
    }else{
      this.comman.ToastMessage("Fail", "Please Fill All Detail");
    }
  }
  resetplotdetail(){
    this.FirstFormGroup.controls['plot_no'].setValue('');
    this.FirstFormGroup.controls['plot_size'].setValue('');
    this.FirstFormGroup.controls['plot_type'].setValue('');
    this.FirstFormGroup.controls['status'].setValue('');
  }
  editplotdetail(){
  if(this.data.add == false){
     this.FirstFormGroup.controls["plot_no"].setValue(this.data.ev.plot_no);
        this.FirstFormGroup.controls["plot_size"].setValue(this.data.ev.plot_size);
        this.FirstFormGroup.controls["plot_type"].setValue(this.data.ev.plot_type);
        this.FirstFormGroup.controls["status"].setValue(this.data.ev.status);
        this.checkUpdate = true;
        this.ckplotno = this.data.ev.plot_no;
        this.ckplotsize = this.data.ev.plot_size;
        this.ckplottype = this.data.ev.plot_type;

  }else{
    this.resetplotdetail();
    this.checkUpdate = false;
  }
}
  

}
